import React from 'react';
import {Link, useLocation} from 'react-router-dom'
import './index.css'

const menu = ['Works', 'Projects', 'Publication', 'Team', 'Job', 'Contact', 'New'];

const Menu = () => {
  const location = useLocation();
  const currentPath = location.pathname.substring(1);
  return (
    <div className={'menu'}>
      {
        menu.map(item =>
          <Link
            to={item.toLowerCase()}
            className={(currentPath === item.toLowerCase() ? 'menu-item-bold' : 'menu-item')}
            key={item}
          >
            {item}
          </Link>
        )
      }
    </div>
  )
};
export default Menu;