import React, {Fragment, useState, useEffect} from 'react';
import {useTransition, animated} from 'react-spring';
import './index.css'
import {Link} from 'react-router-dom';
import image1 from '../../assets/images/DSCF1814.JPG';
import image2 from '../../assets/images/IMG_3032.jpg';
import image3 from '../../assets/images/IMG_3037.jpg';
import image4 from '../../assets/images/IMG_3045.jpg';

const slides = [
  {id: 0, image: image1},
  {id: 1, image: image2},
  {id: 2, image: image3},
  {id: 3, image: image4},
];

const Home = () => {
  const [imageIndex, setImageIndex] = useState(0);
  const transitions = useTransition(slides[imageIndex], item => item.id, {
    from: {opacity: 0},
    enter: {opacity: 1},
    leave: {opacity: 0},
  });

  useEffect(() => {
    const interval = setInterval(() => setImageIndex((imageIndex + 1) % slides.length), 5000);
    return () => {
      clearInterval(interval);
    }
  });

  return (
    <Fragment>
      {transitions.map(({item, props, key}) => (
        <animated.div
          key={key}
          className={'bg'}
          style={{...props, backgroundImage: `url(${item.image})`}}
        />
      ))}
      <div className={'fullscreen'}>
        <Link to='/works'>
          <button className={'home geniso'}> t+m design office</button>
        </Link>
      </div>
    </Fragment>
  )
};

export default Home;